.list-card {
  padding: 0;
  width: 100%;
}
  
.list-card > ul {
  --gap: 1.4rem;
  margin: calc(-1 * var(--gap)) 0 0 calc(-1 * var(--gap));
  width: calc(100% + var(--gap));
  display: inline-flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.list-card > ul > * {
  padding: var(--gap) 0 0 var(--gap);
  width: 25%;
  min-width: 16rem;
  justify-content: center;
  margin: 1rem 0;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #39404c;
  width: 15rem;
  height: 15rem;
  border-radius: 1em;
  margin: 0 auto;
}

.card:hover {
  box-shadow: 0 0 0.7em 0.2em lightblue;
}

.card img {
  width: 75%;
  height: 75%;
  object-fit: cover;
  border-radius: 1em;
  transition: height 0.3s, width 0.3s;
}

.card:hover img {
  object-fit: cover;
  width: 80%;
  height: 80%;
}